<template>
	<div class="bank_victor" id="bank_victor">
		<Tabs value="victor_today" style="margin-top: 15px;">
	        <TabPane label="今日访客" name="victor_today">
	        	<div class="victor_plan_deal ">
	        		<div class="search">
	        			
	        		</div>
	        		<div class="search onlyOne">
	        			<Button type="primary" size="large" @click="victor_create()">新增</Button>
	        		</div>
	        		
	        	</div>
	        	<Table :height="tableHeight" :columns="victo_today_col" :data="victo_today_data" @on-row-click="changeSta"></Table>
	        </TabPane>
	        <TabPane label="历史访客" name="victor_history">
	        	<div class="victor_plan_deal">
	        		<div class="search">
	        			<DatePicker v-model="his_vis_dat" type="daterange"   format="yyyy/MM/dd" placeholder="请选择日期" style="width: 300px" @on-change="his_vis_dat=$event"></DatePicker>
	        			<Button type="primary" size="large" @click="search_his()">搜索</Button>
	        		</div>
	        		<div class="btns">
	        			<Button type="primary" size="large" @click="explore_vis_his"> 导出</Button>
	        		</div>
	        	</div>
	        	<Table :height="tableHeight" :columns="victo_history_col" :data="victo_history_data" ref="table"></Table>
	        </TabPane>
	        
	    </Tabs>
	    <Modal title="新建访客" v-model="victor_appointment" class-name="vertical-center-modal" @on-ok="create_visit()">
	    	<Row :gutter="8" class="lineItem">
		        <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">姓名：</Col>
		        		<Col span="18">
		        			<Input v-model="name" placeholder="来访人姓名"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		       <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">预约时间：</Col>
		        		<Col span="18">
		        			<DatePicker v-model="time" type="datetime" format="yyyy-MM-dd HH:mm" placeholder="预约时间" style="width: 100%;" @on-change="time=$event"></DatePicker>
		        		</Col>
		        	</Row>
		        	
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">电话：</Col>
		        		<Col span="18"><Input v-model="tel" placeholder="来访人电话"></Input></Col>
		        	</Row>

		        </Col>
		        <Col span="12">
		        	<Row type="flex"  align="middle">
		        		<Col span="6">证件：</Col>
		        		<Col span="18"><Input v-model="card" placeholder="来访人证件"></Input></Col>
		        	</Row>

		        	
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">事务：</Col>
		        		<Col span="18">
		        			<Input v-model="reson" placeholder="来访事务"></Input>
		        		</Col>
		        	</Row>

		        	
		        </Col>
		        <Col span="12" >
		        	<Row type="flex"  align="middle">
		        		<Col span="6">楼层：</Col>
		        		<Col span="18"><Input number v-model="foolr" placeholder="来访楼层"></Input></Col>
		        	</Row>

		        	
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">随访人数：</Col>
		        		<Col span="18">
		        			<Input v-model="num" number placeholder="随访人数"></Input>
		        		</Col>
		        	
		        		
		        	</Row>
		        </Col>
		        <Col span="12">
		        	<Row type="flex"  align="middle">
		        		<Col span="6">被访人：</Col>
		        		<Col span="18">
		        			<Select v-model="meetingMem" style="width:100%">
						        <Option v-for="item in memList" :value="item.value" :key="item.value">{{ item.label }}</Option>
						    </Select>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row> 
		    
    	</Modal>
    	<Modal title="更改访客状态"  v-model="visitor_status" class-name="vertical-center-modal" @on-ok="applic_meeting()">
    		<Row :gutter="8" class="lineItem">
    			 <RadioGroup v-model="status">
				        <Radio label="1">
				            <span>已到访</span>
				        </Radio>
			       
				        <Radio label="2">
				            <span>未到访</span>
				        </Radio>
    			</RadioGroup>
		        
		    </Row>
    		
    	</Modal>
	</div>
</template>

<script>
export default{
	data(){
		return{
			victo_today_col:[
				{title: '编号',key: 'code'},{title: '来访人',key: 'name'},{title: '预约时间',key: 'appointment'},{title: '来访人电话',key: 'tel'},{title: '缘由',key: 'reason'},
                {title: '来访楼层',key: 'floor'},{title: '随访人数',key: 'followers'},{title: '被访人',key: 'card'},{title: '到访时间',key: 'time'},{title: '状态',key: 'status'}
			],
			victo_today_data:[],
			victo_history_col:[
				{title: '编号',key: 'code'},{title: '来访人',key: 'name'},{title: '证件号',key: 'idCard'},{title: '预约时间',key: 'appointment'},{title: '来访人电话',key: 'tel'},{title: '缘由',key: 'reason'},
                {title: '来访楼层',key: 'floor'},{title: '随访人数',key: 'followers'},{title: '被访人工号',key: 'card'},{title: '到访时间',key: 'time'},{title: '状态',key: 'status'}
			],
			victo_history_data:[],
			victor_appointment:false,
			name:"",
			time:"",
			tel:"",
			card:"",
			reson:"",
			foolr:"",
			num:"",
			meetingMem:"",
			tableHeight:"",
			visitor_status:false,
			status:"",
			memList:[],
			his_vis_dat:[],
			
		}
	}
	,mounted(){
		let h = document.documentElement.clientHeight || document.body.clientHeight;
		this.tableHeight=h-180;
		this.loadToday();
		this.loadMemList()
	},
	methods:{
		loadToday(){
			let date=new Date();
			let year=date.getFullYear();
			let mon=date.getMonth()+1;
			let day=date.getDate();
			let dateStr=year+"-"+mon+"-"+day;
			let data={
				time:dateStr
			};
			this.Axios.post("/api/Event/get_today_reser",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
				let dat=res.data.HttpData.data;
				this.victo_today_data=[];
				for(var value of dat){
					let arr={
						code:value.id,
						name:value.name,
						appointment:value.time.replace("T"," "),
						tel:value.phone,
						reason:value.event,
						floor:value.floor,
						followers:value.followUp,
						card:value.bsname,
						time:value.visitTime?value.visitTime.replace("T"," "):"",
						status:this.returnVisSta(value.status) 
					}
					if(value.status){
						this.victo_today_data.push(arr)
					}else{
						this.victo_today_data.unshift(arr)
					}
					
				}
			})
		},loadMemList(){
			this.Axios.get("/api/Event/get_this_visitInfor",{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
				let dat=res.data.HttpData.data;
				for(var value of dat){
					let obj={
						value:value.workNo,
						label:value.name
					}
					this.memList.push(obj)
				}
			})
		},create_visit(){
			let data={
				name:this.name,
				time:this.time,
				phone :this.tel,
				license:this.card,
				events :this.reson,
				floor :this.foolr,
				followUp :this.num,
				memberId :this.meetingMem
			}
			this.Axios.post("/api/Event/add_reservation",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
				let dat=res.data.HttpData.data;
				if(dat==1){
					this.$Message.success("操作成功");
					this.loadToday()
				}else{
					 this.$Message.error('操作失败');
				}
			})
			
		},changeSta(parag){
		},
		search_his(){
			let data={
				start:this.his_vis_dat[0],
				end :this.his_vis_dat[1]
			}
			this.Axios.post('/api/Event/get_his_visitInfor',data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
				let dat=res.data.HttpData.data;
				for(var value of dat){
					let arr={
						code:value.id,
						name:value.name,
						idCard:value.license,
						appointment:value.time.replace("T"," "),
						tel:value.phone,
						reason:value.event,
						floor:value.floor,
						followers:value.followUp,
						card:value.memberId,
						time:value.visitTime?value.visitTime.replace("T"," "):"",
						status:this.returnVisSta(value.status) 
					}
					this.victo_history_data.push(arr);

				}

			})
			
		},explore_vis_his(){
			this.$refs.table.exportCsv(
				{
					filename:"历史访客"+this.his_vis_dat.join("-")
				}
			)
		},
		returnVisSta(bool){
			let txt=''
			if(bool){
				txt='已到访'
			}else{
				txt='未到访'
			}
			return txt
		},
		victor_create(){
			this.victor_appointment=true;
		}
	}
}
</script>

<style lang="scss" scoped="scoped">
	#bank_victor{
		width: 100%;
		height: 100%;
		padding: 0 15px;
		.ivu-tabs{
			height: 100%;
		}
		.victor_plan_deal{
			display: flex;
			justify-content: space-between;
			margin-bottom:15px ;
			.search{
				button{
					margin-left: 10px;
					padding: 0 15px;
					min-width: 71px;
					width: auto;
					color: #fff;
				}
				&.onlyOne{
					button{
						margin-left: 0px;
					}
				}
			}
			.btns{
				button{
					margin-left: 10px;
					padding: 0 15px;
					min-width: 71px;
					width: auto;
					color: #fff;
				}
				
			}
		}
	}
</style>


