		        			<DatePicker v-model="met_end_time" type="datetime" format="yyyy-MM-dd HH:mm" placeholder="结束时间" style="width: 100%;"></DatePicker>
		        		</Col>
		        		
		        		
		        	</Row>
		        </Col>
		    </Row> 
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">会议人员：</Col>
		        		<Col span="21">
		        			<Row type="flex" align="middle">
		        				<Col span="10">
		        					<Select v-model="memdepart" multiple placeholder="选择部门" style="width:100%">
								        <Option  v-for="item in mem_depart" :value="item.value" :key="item.value">{{ item.label}}</Option>
								    </Select>
		        				</Col>
		        				<Col span="14">
		        					<Select v-model="meetingMem" multiple placeholder="选择人员" style="width:100%" >
								        <Option v-for="item in met_mem_list" :value="item.value" :key="item.value">{{ item.label }}</Option>
								    </Select>
		        				</Col>
		        				
		        			</Row>
		        			
		        			
		        		</Col>
		        		
		        	</Row>
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="4"><Checkbox v-model="join"></Checkbox>领导与否：</Col>
		        		<Col span="20">
		        			<Input :disabled="!join"  placeholder="备注信息" v-model="met_leader"></Input>
		        		</Col>
		        	</Row>
		        	
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">通知方式：</Col>
		        		<Col span="21">
		        			<Select v-model="meetingNot" multiple placeholder="选择会议通知方式" style="width:100%">
						        <Option v-for="item in meetNotWay" :value="item.value" :key="item.value">{{ item.label }}</Option>
						    </Select>
		        		</Col>
		        	</Row>
		        	
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">会议服务：</Col>
		        		<Col span="21">
		        			<Select v-model="meetingServe" multiple placeholder="选择会议服务" style="width:100%">
						        <Option v-for="item in meetingSer" :value="item.value" :key="item.value">{{ item.label }}</Option>
						    </Select>
		        		</Col>
		        	</Row>
		        	
		        </Col>
		    </Row>
		    <!--<Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">文件上传：</Col>
		        		<Col span="21">
		        			<a  download="" href="http://127.0.0.1:9001/22.xlsx" @click="down">汶口按摩</a>
		        			<input type="file" multiple="multiple"  id="imgUp"/>
		        			
		        			<span @click="upload">
		        				确认上传
		        			</span>
		        			<!--<Upload
		        				ref="upload"
					            :before-upload="handleUpload"
					            :on-success="handleSccuss"
					            :on-progress="handlePros"
					            action="http://localhost:9000/api/file/upload">
					            <Button type="ghost" icon="ios-cloud-upload-outline">选择文件进行上传</Button>
					        </Upload>
					        <div v-if="file !== null">Upload file: {{ file.name }} <Button type="text" @click="upload" :loading="loadingStatus">{{ loadingStatus ? 'Uploading' : 'Click to upload' }}</Button></div>
		        		</Col>
		        	</Row>
		        	
		        </Col>
		    </Row>-->
		    
    	</Modal>
    	<Modal title="会议审批"  v-model="meeting_room_fp" class-name="vertical-center-modal" @on-ok="applic_meeting()">
    		<Row :gutter="8" class="lineItem">
    			 <RadioGroup v-model="agree">
			        
				        <Radio label="1">
				            <span>同意</span>
				        </Radio>
			       
				        <Radio label="2">
				            <span>不同意</span>
				        </Radio>
    			</RadioGroup>
		        
		    </Row>
    		<Row :gutter="8" class="lineItem" v-show="showRoom">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">会议室：</Col>
		        		<Col span="21">
		        			<Select v-model="meetingRoom"   style="width:100%">
						        <Option v-for="item in meeting_list" :value="item.value" :key="item.value">{{ item.label }}</Option>
						    </Select>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
    	</Modal>	
		<Modal title="新建会议室"  v-model="meeting_room" class-name="vertical-center-modal"  @on-ok="meetingRoomDeal()">
	    	<Row :gutter="8" class="lineItem">
		        <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">编号：</Col>
		        		<Col span="18">
		        			<Input :disabled="isEdict" placeholder="编号" v-model="met_rom_code"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		        <Col span="12">
		        	<Row type="flex"  align="middle">
		        		<Col span="6">名称：</Col>
		        		<Col span="18">
		        			<Input  placeholder="名称" v-model="met_rom_name"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">所在楼层：</Col>
		        		<Col span="18"><Input  placeholder="所在楼层" v-model="met_rom_floor"></Input></Col>
		        	</Row>
		        </Col>
		        <Col span="12">
		        	<Row type="flex"  align="middle">
		        		<Col span="6">面积：</Col>
		        		<Col span="18"><Input  placeholder="面积(㎡)" v-model="met_rom_space"></Input></Col>
		        	</Row>
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">容纳人数：</Col>
		        		<Col span="18">
		        			<Input  placeholder="容纳人数" v-model="met_rom_num"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		        <Col span="12" >
		        	<Row type="flex"  align="middle">
		        		<Col span="6">IP地址：</Col>
		        		<Col span="18"><Input  placeholder="IP地址127.0.0.1" v-model="met_rom_ip"></Input></Col>
		        	</Row>
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem">
		        <Col span="12">
		        	<Row type="flex" align="middle">
		        		<Col span="6">联系人：</Col>
