		        			<Input  placeholder="物业联系人" v-model="met_rom_propPeo"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		        <Col span="12">
		        	<Row type="flex"  align="middle">
		        		<Col span="6">联系电话：</Col>
		        		<Col span="18">
		        			<Input  placeholder="联系电话" v-model="met_rom_propTel"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row> 
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">联系邮箱：</Col>
		        		<Col span="21">
		        			<Input  placeholder="联系邮箱" v-model="met_rom_propMail"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
		    
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">支持服务：</Col>
		        		<Col span="21">
		        			<Select v-model="meetingServe_rom" multiple style="width:100%">
						        <Option v-for="item in meetingSer" :value="item.value" :key="item.value">{{ item.label }}</Option>
						    </Select>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
		    <Row :gutter="8" class="lineItem" v-if="isApproval">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">会议室：</Col>
		        		<Col span="21">
		        			<Select v-model="meetingServe" multiple style="width:100%">
						        <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
						    </Select>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
    	</Modal>
		<Modal title="新建会议服务"  v-model="meeting_server" class-name="vertical-center-modal" @on-ok="applic_meeting_serve()" >
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">服务名称：</Col>
		        		<Col span="21">
		        			<Input v-model="new_server" placeholder="服务名称"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
    	</Modal>
		<Modal title="删除会议室"  v-model="dele_meeting" class-name="vertical-center-modal" @on-ok="detele_meeting()" >
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	删除会议室？
		        </Col>
		    </Row>
    	</Modal>
    	<Modal title="资料上传"  v-model="uploadfile" class-name="vertical-center-modal" @on-ok="uploadFile()">
		    <Row :gutter="8" class="lineItem">
		    	<Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">文件选择：</Col>
		        		<Col span="21">
		        			<input type="file" multiple="multiple" id="upload" @change="fileList"/>
		        			
		        			
		        		</Col>
		        	</Row>
		        	<Row type="flex" align="middle">
		        		<Col span="3"></Col>
		        		<Col span="21">
		        			<ul>
		        				<li v-for="item in filelist">{{item}}</li>
		        			</ul>
		        		</Col>
		        	</Row>
		        </Col>
		    	
		       
		    </Row>
    	</Modal>
    	<Modal title="资料下载"  v-model="downloadfile" class-name="vertical-center-modal" @on-ok="downloadFile()">
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">资料列表:</Col>
		        		<Col span="21">
		        			<ul>
		        				<li v-for="item in downList">
		        					<a :href="item.url" :download="item.name">{{item.name}}</a>
		        				</li>
		        				
		        			</ul>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
    	</Modal>
	</div>
</template>
	
<script>
	export default{
		data(){
			return{
				
				meet_plan_col:[
					{title: '申请人',key: 'name'},{title: '会议主题',key: 'theme'},{title: '会议类型',key: 'type'},{title: '会议内容',key: 'content'},{title: '开始时间',key: 'start'},
                    {title: '结束时间',key: 'end'},{title: '联系人',key: 'contact'},{title: '会议室',key: 'room'},{title: '会议状态',key: 'status'}
				],
				meet_plan_data_sort:[],
				meet_plan_data:[],
				meet_approval_col:[
					{title: '申请人',key: 'name'},{title: '会议主题',key: 'theme'},{title: '会议类型',key: 'type'},{title: '会议内容',key: 'content'},{title: '开始时间',key: 'start'},
                    {title: '结束时间',key: 'end'},{title: '联系人',key: 'contact'}
				],
				meet_approval_data:[],
				meet_approval_data_sort:[],
				meet_mark_col:[
					{title: '申请人',key: 'name'},{title: '会议主题',key: 'theme'},{title: '会议类型',key: 'type'},{title: '会议内容',key: 'content'},{title: '开始时间',key: 'start'},
                    {title: '结束时间',key: 'end'},{title: '联系人',key: 'contact'},{title: '会议室',key: 'room'},{title: '会议状态',key: 'status'}
				],
				meet_mark_data:[],
				meet_set_col:[
					{title: '编号',key: 'code'},{title: '名称',key: 'name'},{title: '楼层',key: 'floor'},{title: '面积',key: 'area'},{title: '容纳人数',key: 'num'},
                    {title: 'IP地址',key: 'ip'},{title: '物业联系人',key: 'contact'},{title: '联系电话',key: 'tel'},{title: '联系邮箱',key: 'mail'},{title: '支持服务',key: 'serve'},
                    {title: '操作',key: 'deal',render: (h, params) => {
                            return h('div', [
                                h('span', {
                                    style: {
                                        color: '#ed3f14',
                                        cursor:'pointer'
                                    },
                                    on: {
                                        click: (e) => {
                                        	e.stopPropagation();
                                        	this.deleteItem(params)
                                        }
                                    }
                                }, '删除'),
                               
                            ]);
                        }
                    }
				
				],
				meet_set_data:[],
				meet_server_col:[
					{title: '编号',key: 'code'},{title: '名称',key: 'name'},{title: '操作',key: 'deal',render:(h,params)=>{
						return h('span',{
							 style: {
                                        color: '#ed3f14',
                                        cursor:'pointer'
                                    },
                                    on: {
                                        click: (e) => {
                                        	e.stopPropagation();
                                        	this.deleteItemServ(params)

                                        }
                                    }
						},"删除")
					}}
				],
				met_app_txt:'',
				meet_server_data:[],
				mem_depart:[],
				memdepart:[],
				meetingSer:[],
				met_mem_list:[],
				meetNotWay:[{value:"0",label:"短信"},{value:"1",label:"邮件"}],
				meetingType:[{value:"0",label:"培训"},{value:"1",label:"一级"},{value:"2",label:"二级"},{value:"3",label:"其他"}],
				met_typ_data:'',//会议类型
				met_appli:'',//会议申请人
				
				met_topic:"",//会议主题
				met_contact:"",//会议联系人
				met_cont_tel:"",//会议联系电话
				met_content:"",//会议内容
				met_start_time:"",//会议开始时间
				met_end_time:"",//会议结束时间
				meetingMem:[],//会议与会人员
				met_leader:"",//会议领导
				dele_meeting:false,
				agree:'',
				showRoom:false,
				isEdict:false,
				uploadfile:false,
				downloadfile:false,
				meetingRoom:"",//会议室
				meeting_list:[],
				markData:[],
				appli_meeting_id:null,
				met_rom_code:'',
				met_rom_name:'',
				met_rom_floor:'',
				met_rom_space:'',
				met_rom_num:'',
				met_rom_ip:"",
				met_rom_propPeo:"",
				met_rom_propTel:"",
				met_rom_propMail:"",
				meetingServe_rom:[],
				new_server:'',
				dele_room_id:"",
				dele_room_index:"",
				easyData:{
					 shortcuts: [
                        {
                           	text: '1周',
                            value () {
                                const end = new Date();
                                const start = new Date();
                                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                                return [start, end];
                            }
                        },
                        {
                            text: '1月',
                            value () {
                                const end = new Date();
                                const start = new Date();
                                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                                return [start, end];
                            }
                        }
                    ]
				},
				meeting_appointment:false,
				
				meetingRoomDealId:null,
				meetingServerDealId:null,
				server:'',
				meetingServe:[],
				meetingNot:[],
				meeting_room_fp:false,
				join:false,
				model1:'',
				isApproval:false,
				meeting_room:false,
				meeting_server:false,
				tableHeight:300,
				meetingServerId:'',
				met_plan_txt:'',
				dealMeetingIndex:0,
				filelist:[],
				fileDealId:0,
				downList:[]
				
				
			}
		},mounted(){
			let h = document.documentElement.clientHeight || document.body.clientHeight;
			this.tableHeight=h-180;
			this.loadMeetingPlan();
			this.loadMeetingAppli();
			this.loadMeetingServer();
			this.loadmem_depart();
			this.loadMeetingRoom();
			this.loadMeetingRoomInfor();
			this.memInfor();
		},watch:{
			memdepart(){
				let arr="'"+this.memdepart.join("','")+"'";
				this.Axios.post('/api/Event/get_mem_dep_mul',{department:arr},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.met_mem_list=[];
					for(var val of dat){
						let obj={
							value:val.workNo,
							label:val.name
						}
						this.met_mem_list.push(obj);
					}
				})
			},
			agree(){
				if(this.agree==1){
					this.showRoom=true;
				}else{
					this.meetingRoom='';
					this.showRoom=false;
				}
			},met_plan_txt(){
				var _search=this.met_plan_txt;
				if(!_search){
					this.meet_plan_data_sort=this.meet_plan_data;
				}
			},met_app_txt(){
				var _search=this.met_app_txt;
				if(!_search){
					this.meet_approval_data_sort=this.meet_approval_data;
				}
			}
			
		},
		methods:{
			
           fileList(){

            	this.filelist=[];
				var files=document.getElementById("upload").files; 
				var leng=files.length;
				if(leng>1){
					for(var i=0;i<files.length;i++){
						var name=files[i].name;
						this.filelist.push(name);
					}
				}
            },
            uploadFileShow(){
            	this.uploadfile=true;
            },downloadFileShow(){
            	this.downloadfile=true;
            	let data={
            		id:this.fileDealId
            	}
            	this.Axios.post("/api/Event/get_file_name",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
            		this.downList=[];
            		let dat=res.data.HttpData.data[0].record;
            		
            		if(dat){
            			dat=dat.split(",")
            			for(var value of dat){
	            			let obj={
	            				name:value,
	            				url:"http://localhost:9001/"+value
	            			}
	            			this.downList.push(obj);
	            		}
            		}else{
            			this.downList.push({
            				name:"暂无资料下载",
	            			url:"#"
            			});
            		}
            		
            	})
            },getFileIndex(currentRow){
            	this.fileDealId=currentRow.id;
            },
            uploadFile(){
            	var that=this;
            	var files=document.getElementById("upload").files;
            	var leng=files.length;
            	var formData = new FormData();
            	
            	for(var i=0;i<files.length;i++){
					var name=files[i].name;
		            formData.append('file', files[i]); 
				}
            	
            	$.ajax({
		            type: 'post',
		            url: 'http://localhost:9000/api/file/upload',
		            data: formData,
		            cache: false,
		            processData: false, // 不处理发送的数据，因为data值是Formdata对象，不需要对数据做处理
		            contentType: false, // 不设置Content-type请求头
		            success: function (dt) {
//		            	var dat=eval(dt);
		            	var dat=JSON.parse(dt).Message;
		            	var name=dat.replace(/\[|]|"/g,"");
		            	var data={
		            		id:that.fileDealId,
		            		name:name
		            	}
		            	that.Axios.post("/api/Event/upd_file_name",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
		            		let dat=res.data.HttpData.data;
							if(dat==1){
								that.$Message.success("操作成功");
								that.loadMeetingRoomInfor();
							}else{
								 that.$Message.error('操作失败');
							}
		            	})
		            }
		         });

            },downloadFile(){
            	
            },
			search(){
				let _search=this.met_plan_txt;
				if(_search){
					let sort= this.meet_plan_data.filter(function(item){
						return Object.keys(item).some(function(key){
							return String(item[key]).toLowerCase().indexOf(_search)>-1
						})
					})
					this.meet_plan_data_sort=sort;
				}
			},search_app(){
				let _search=this.met_app_txt;
				if(_search){
					let sort= this.meet_approval_data.filter(function(item){
						return Object.keys(item).some(function(key){
							return String(item[key]).toLowerCase().indexOf(_search)>-1
						})
					})
					this.meet_approval_data_sort=sort;
				}
			},
			createMeeting(deal){
				if(deal==0){
					this.isApproval=false;
				}else{
					this.isApproval=true;
				}
				this.meeting_appointment=true
			},
			deal_meeting(){
				this.meeting_room=true;
				this.meetingRoomDealId=0;
				this.isEdict=false;
			},
			deal_server(deal){
				this.meeting_server=true;
				this.meetingServerDealId=0;
				//新建服务
			},memInfor(){
				this.Axios.post('/api/Event/get_mem_infor_pc',{workNo :localStorage.userName},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					this.met_appli=res.data.HttpData.data[0].name
				})
			},
			loadMeetingPlan(){
				this.Axios.post('/api/Event/get_user_meeting',{"userName":window.localStorage.userName},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.meet_plan_data=[];
					for (var value of dat){
						let arr={
							name:value.name,
							theme:value.topic,
							type:this.returnMeetingType(value.type),
							content:value.meetingContent,
							start:value.begintime.replace("T"," "),
							end:value.endTime.replace("T"," "),
							contact:value.contact,
							room:value.room,
							status:this.returnMeetingSta(value.status) 
						}
						this.meet_plan_data.push(arr)
					}
					this.meet_plan_data_sort=this.meet_plan_data;
				})
			},loadMeetingAppli(){
				this.Axios.get("/api/Event/get_all_appMet",{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.meet_approval_data=[];
					for(var value of dat){
						let arr={
							id:value.id,
							name:value.name,
							theme:value.topic,
							type:this.returnMeetingType(value.type),
							content:value.meetingContent,
							start:value.begintime.replace("T"," "),
							end:value.endTime.replace("T"," "),
							contact:value.contact
						}
						this.meet_approval_data.push(arr)
					}
					this.meet_approval_data_sort=this.meet_approval_data;

				})
			},
			loadMeetingServer(){
				this.Axios.post('/api/Event/get_meet_serve',{},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.meet_server_data=[];
					this.meetingSer=[];
					for(var val of dat){
						let obj={
							value:val.id,
							label:val.text
						}
						let arr={
							code:val.id,
							name:val.text
						}
						this.meet_server_data.push(arr);
						this.meetingSer.push(obj);
					}
				})
			},loadmem_depart(){
				this.Axios.post('/api/Event/get_all_depart',{},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					for(var val of dat){
						let obj={
							value:val.department,
							label:val.department
						}
						this.mem_depart.push(obj)
					}
				})
			},loadMeetingRoom(){
				this.Axios.post("/api/Event/get_room_infor",{isAll:"all"},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					for (var value of dat){
						let arr={
							value:value.id,
							label:value.name
						}
						this.meeting_list.push(arr)
					}
				})
			},loadMeetingRoomInfor(){
				this.Axios.post("/api/Event/get_room_infor",{isAll:"all"},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					this.meet_set_data=[];
					let dat=res.data.HttpData.data;
					for (var value of dat){
						let arr={
							code:value.id,
							name:value.name,
							floor:value.floor,
							area:value.space,
							num:value.capacity,
							ip:value.ipAddr,
							contact:value.propName,
							tel:value.propPhone,
							mail:value.propName,
							serveId:value.service,
							serve:value.sertxt
						}
						
					this.meet_set_data.push(arr)
					}
					
				})
			},meetingRoomDeal(){
				//会议室新建
				this.isEdict=false;
				let url;
				if(this.meetingRoomDealId==0){
//					新增
					url="/api/Event/set_new_meetng_room";
				}else{
//					编辑
					url="/api/Event/upd_meetng_room";
					
				}
				let data={
						id:this.met_rom_code,
						name:this.met_rom_name,
						floor:this.met_rom_floor,
						space:this.met_rom_space,
						capacity:this.met_rom_num,
						ipAddr:this.met_rom_ip,
						propName:this.met_rom_propPeo,
						propPhone:this.met_rom_propTel,
						propMail:this.met_rom_propMail,
						service:this.meetingServe_rom.toString()
				};
				this.Axios.post(url,data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
						let dat=res.data.HttpData.data;
						if(dat==1){
							this.$Message.success("操作成功");
							this.loadMeetingRoomInfor();
						}else{
							 this.$Message.error('操作失败');
						}
				})
			},
			applic_meeting(){
//				会议审批
				var data={
					id:this.appli_meeting_id,
					approver :localStorage.userName,
					result :this.agree,
					room :this.meetingRoom
				}
				this.Axios.post("/api/Event/set_this_meet_result",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					if(dat==1){
						this.$Message.success("操作成功")
//						this.dealMeetingIndex
						this.meet_approval_data.splice(this.dealMeetingIndex,1)
						this.loadMeetingPlan();
					}else{
						 this.$Message.error('操作失败');
					}
				})
			},applic_meeting_serve(){
				//新建会议室服务
				let data={},url;
				if(this.meetingServerDealId==0){
					//新增
					data={
						text:this.new_server
					};
					url="/api/Event/set_new_meet_serve";
				}else{
					data={
						id:this.meetingServerId,
						text:this.new_server
					};
					url="/api/Event/upd_meet_serve";
				}
				this.Axios.post(url,data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					if(dat==1){
						this.$Message.success("操作成功");
						this.loadMeetingServer();
					}else{
						 this.$Message.error('操作失败');
					}
				})
				
			},
			edict_meeting_server(para){
				//编辑服务
				this.meetingServerDealId=1;

				this.meeting_server=true;
				this.new_server=para.name;
				this.meetingServerId=para.code;
				
			},
			edict_meeting_room(para){
				this.meeting_room=true;
				this.met_rom_code=para.code;
				this.met_rom_name=para.name;
				this.met_rom_floor=para.floor;
				this.met_rom_space=para.area;
				this.met_rom_num=para.num;
				this.met_rom_ip=para.ip;
				this.met_rom_propPeo=para.contact;
				this.met_rom_propTel=para.tel;
				this.met_rom_propMail=para.mail;
				this.meetingServe_rom= para.serveId.split(",").map(Number);
				this.meetingRoomDealId=1;
				this.isEdict=true;

			},
			searchMark(){
				let start=this.markData[0];
				let end=this.markData[1];
				let data={
					startTime:start,
					endTime:end
				};
				this.Axios.post('/api/Event/get_all_meet',data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.meet_mark_data=[]
					for(var value of dat){
						let arr={
							id:value.id,
							name:value.applicant,
							theme:value.topic,
							type:this.returnMeetingType(value.type),
							content:value.meetingContent,
							start:value.begintime.replace("T"," "),
							end:value.endTime.replace("T"," "),
							contact:value.contact,
							room:value.room,
							status:this.returnMeetingSta(value.status)
						}
						this.meet_mark_data.push(arr)
						
					}

				})

			},deleteItem(para){
//				
				//删除会议室
				this.dele_meeting=true;
				this.dele_room_id=para.row.code;
				this.dele_room_index=para.index;
				
			},detele_meeting(){
				//删除会议modal
				let data={
					id:this.dele_room_id
				}
				this.Axios.post("/api/Event/del_meetng_room",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					if(dat==1){
						this.$Message.success("操作成功")
						this.meet_set_data.splice(this.dele_room_index,1)
					}else{
						 this.$Message.error('操作失败');
					}
				})
			},
			deleteItemServ(para){
				let data={
					id:para.row.code
				}
				this.Axios.post("/api/Event/del_meet_serve",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					if(dat==1){
						this.$Message.success("操作成功");
						this.loadMeetingServer();
					}else{
						 this.$Message.error('操作失败');
					}
				})
			},
			sureNewMeeting(){

				let data={
					memberId :localStorage.userName,
					others :this.meetingMem.toString(),
					begintime :this.met_start_time,
					endTime :this.met_end_time,
					meetType :this.met_typ_data,
					contact :this.met_contact,
					contactPhone :this.met_cont_tel,
					service :this.meetingServe.toString(),
					content :this.met_content,
					isLeader :this.join,
					mark :this.met_leader,
					topic:this.met_topic,
					notify :this.meetingNot.toString(),
				}
				
				this.Axios.post('/api/Event/add_meet',data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					if(dat==1){
						this.$Message.success("操作成功");
						this.loadMeetingAppli();
					}else{
						 this.$Message.error('操作失败');
					}
					
				})

				
			},
			appli_meeting(para,index){
				this.meeting_room_fp=true;
				this.appli_meeting_id=para.id;
				this.dealMeetingIndex=index;
			},
			
			returnMeetingType(id){
				let type="";
				switch (id)
				{
					case 0:
						type='培训'
					break;
					case 1:
						type='一级'
					break;
					case 2:
						type='二级'
					break;
					case 3:
						type='其他'
					break;
				}
				return type;
			},returnMeetingSta(bool){
				let txt="";
				if(!bool){
					txt="正常"
				}else{
					txt="结束"
				}
				return txt
			},filterPlan(id){
				
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
	#bank_meet{
		width: 100%;
		padding: 0 15px;
		height: 100%;
		.ivu-tabs {
			height: 100%;
		}
		.meet_plan_deal{
			display: flex;
			justify-content: space-between;
			margin-bottom:15px ;
			.search{
				button{
					margin-left: 10px;
					padding: 0 15px;
					min-width: 71px;
					width: auto;
					color: #fff;
				}
				&.onlyOne{
					button{
						margin-left: 0px;
					}
				}
			}
			.btns{
				button{
					margin-left: 10px;
					padding: 0 15px;
					min-width: 71px;
					width: auto;
					color: #fff;
				}
				
			}
		}
		
		
	}
	
</style>



