<template>
	<div class="bank_property" id="bank_property">
		<!--<Tabs value="list" style="margin-top: 15px;">
	        <TabPane label="资产清单" name="list">-->
	        	<div class="property_plan_deal" style="margin-top: 15px;">
	        		<div class="search">
	        			<Input  placeholder="" style="width: 300px" v-model="proSearch"></Input>
	        			<Button type="primary" size="large" @click="proSearchDeal">搜索</Button>
	        			 
	        		</div>
	        		<div class="btns">
	        			<Button type="primary" size="large" @click="scanModal()">扫标签导入</Button>
	        			<Button type="primary" size="large" @click="showFileModal(1)">导入低值易耗</Button>
	        			<Button type="primary" size="large" @click="showFileModal(0)">导入固定资产</Button>
	        			
	        		</div>
	        	</div>
	        	<Table  :height="tableHeight" :columns="property_list_col" :data="property_list_col_data_sort"></Table>
	        	<Row :gutter="16" type="flex" justify="center" style="text-align: center;height: 40px;line-height: 40px;font-size: 14px;" v-if="numCount">
			        <Col span="4">已入库: <span class="num"> {{yrk}}</span>个</Col>
			        <Col span="4">已导入:<span class="num">{{ydr}}</span>个</Col>
			        <Col span="4">差异:<span class="num">{{diff}}</span>个</Col>
			        <Col span="4">正常:<span class="num">{{nomal}}</span>个</Col>
			        <Col span="4">报废:<span class="num">{{unUse}}</span>个</Col>
			        <Col span="4">已处置:<span class="num">{{dealed}}</span>个</Col>
			    </Row>
			    <Table :height="tableHeight" :columns="property_list_col_bot" :loading="loading" :data="upload_file_data" @on-row-click="loadFileDeal" v-if="fileDeal"></Table>
			    <Table :height="tableHeight" :columns="property_list_col_bot" :loading="loading"   v-if="scanLoad"></Table>
	    <Modal title="文件导入"  v-model="fileLoad" class-name="vertical-center-modal" @on-ok="fileLoadFun()">
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">文件选择:</Col>
		        		<Col span="21">
		        			<input type="file"  id="fileLoad"/>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
    	</Modal>
    	<Modal title="发卡"  v-model="postCard" class-name="vertical-center-modal" @on-ok="postCardDeal()">
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	<Row type="flex" align="middle">
		        		<Col span="3">卡号:</Col>
		        		<Col span="21">
		        			 <Input  placeholder="卡号" style="width: 100%;" v-model="rfCardNo"></Input>
		        		</Col>
		        	</Row>
		        </Col>
		    </Row>
    	</Modal>
    	<Modal title="报废"  v-model="unused" class-name="vertical-center-modal" @on-ok="unUnseDeal()">
		    <Row :gutter="8" class="lineItem">
		        <Col span="24">
		        	{{unsedTxt}}确定报废？
		        </Col>
		    </Row>
    	</Modal>
	</div>
</template>
	
<script>
	export default{
		data(){
			return{
				property_list_col:[
					{title: '资产编号',key: 'code'},{title: '资产名称',key: 'name'},{title: '资产类型',key: 'type'},{title: '单位',key: 'unit'},
					{title: '部门',key: 'depart'},{title: '数量',key: 'num'},{title: '卡号',key: 'cardNo'},{title: '是否闲置',key: 'isUse'},{title: '是否可用',key: 'canUse'},
					{title: '发卡时间',key: 'date'}
				],
				property_list_col_data:[],
				property_list_col_data_sort:[],
				property_list_col_bot:[
					{title: '单位',key: 'unit'},{title: '部门',key: 'department'},{title: '网点或科室',key: 'branch'},
					{title: '财产分类',key: 'assetType'},{title: '财产编号',key: 'id'},{title: '财产名称',key: 'assetName'},{title: '型号',key: 'model'},
					{title: '是否在用',key: 'isUsing'},{title: '操作',key: 'deal'}
//					{title: '可否使用',key: 'canUse'}
					
				],
				upload_file_data:[],
				yrk:0,
				ydr:0,
				diff:0,
				nomal:0,
				unUse:0,
				dealed:0	,
				fileLoad:false,
				tableHeight:300,
				upFileId:0,
				loading:false,
				postCard:false,
				unused:false,
				numCount:false,
				fileDeal:false,
				scanLoad:false,
				h:0,
				ind:0,
				proSearch:"",
				rfCardNo:"",
				assetId: "",
				assetName: "",
				assetType: "",
				branch: "",
				canUse: "",
				count: "",
				deal: "",
				department: "",
				id: "",
				isUsing: "",
				keepPlace: "",
				keeper: "",
				model: "",
				orginalVal: "",
				purchase: "",
				remark: "",
				startUse: "",
				unit: "",
				type:"",
				unsedTxt:""
			}
		},mounted(){
			this.h = document.documentElement.clientHeight || document.body.clientHeight;
			this.tableHeight=this.h-180;
			this.loadAsset();
		},watch:{
			proSearch(){
				let _search=this.proSearch;
				if(!_search){
					this.property_list_col_data_sort=this.property_list_col_data;
				}
			}
		},
		methods:{
			proSearchDeal(){
				let _search=this.proSearch;
				if(_search){
					let sort= this.property_list_col_data.filter(function(item){
						return Object.keys(item).some(function(key){
							return String(item[key]).toLowerCase().indexOf(_search)>-1
						})
					})
					this.property_list_col_data_sort=sort;
				}
			},
			loadAsset(){
				this.Axios.get("/api/Event/get_asset_list",{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.property_list_col_data=[];
					for (var value of dat){
						let obj={
							code:value.id,
							name:value.assetName,
							type:value.assetType,
							unit:value.unit,
							depart:value.department,
							num:value.count,
							cardNo:value.rfCard,
							isUse:value.isUsing?"是":"否",
							canUse:value.canUse?"否":"是",
							date:value.rfTime.replace("T" ," ")
						}
						this.property_list_col_data.push(obj)
						this.property_list_col_data_sort=this.property_list_col_data;
					}
				})
			},showFileModal(id){
				this.fileLoad=true;
				this.tableHeight=(this.h-180)/2;
				this.numCount=true;
				this.fileDeal=true;
				this.upFileId=id;
				this.scanLoad=false;
			},scanModal(){
				this.fileDeal=false;
				this.scanLoad=true;
				this.numCount=true;
				this.tableHeight=(this.h-180)/2;
				
			},
			fileLoadFun(){
				this.loading=true;
				var files=document.getElementById("fileLoad").files;
				
            	
            	var formData = new FormData();
            	var that=this;
            	
		        formData.append('data', files[0]); 
		        formData.append('type',that.upFileId ); 
				
            	
            	$.ajax({
		            type: 'post',
		            url: 'http://localhost:9000/JK/tag/stt',
		            data: formData,
		            cache: false,
		            processData: false, // 不处理发送的数据，因为data值是Formdata对象，不需要对数据做处理
		            contentType: false, // 不设置Content-type请求头
		            success: function (dt) {
		            	var dat=JSON.parse(dt)
		            	if(dat.code==0){
		            		that.loading=false;
		            		that.$Message.error('文件读取失败！');
		            	}else{
		            		let res=JSON.parse(dat.Message);
		            		that.upload_file_data=[];
		            		for (var value of res){
		            			let obj={
		            				unit:value.Unit,
		            				department:value.Department,
		            				branch:value.Branch,
		            				assetType:value.AssetType,
		            				id:value.ID,
		            				type:that.upFileId,
		            				assetName:value.AssetName,
		            				model:value.Mode,
		            				count:value.Count,
		            				orginalVal:value.OrginalVal,
		            				startUse:value.StartUse,
		            				assetId:value.AssetId,
		            				isUsing:value.IsUsing=="0"?"是":"否",
		            				canUse:value.CanUse=="0"?"是":"否",
		            				keeper:value.Keeper,
		            				keepPlace:value.KeepPlace,
		            				purchase:value.Purchase,
		            				remark:value.Remark,
		            				deal:value.CanUse=="0"?"发卡":"报废"	            				
		            			}
		            			that.upload_file_data.push(obj)
		            		}
		            		that.loading=false;		            				            		
		            	}

		            }
		         });
			},postCardDeal(){
				let mydate=new Date();
				let year=mydate.getFullYear();
				let mon=mydate.getMonth()+1;
				let day=mydate.getDate();
				let hours=mydate.getHours();
				let min=mydate.getMinutes();
				let rfTime=year+"/"+mon+"/"+day+" "+hours+":"+min;
				let data={
					assetId:this.assetId,
					assetName:this.assetName,
					assetType:this.assetType,
					branch:this.branch,
					type:this.type,
					canUse:this.canUse=="是"?0:1,
					count:this.count,
					department:this.department,
					id:this.id,
					isUsing:this.isUsing=="是"?0:1,
					keepPlace:this.keepPlace,
					keeper:this.keeper,
					model:this.model,
					orginalVal:this.orginalVal,
					purchase:this.purchase,
					remark:this.remark,
					startUse:this.startUse,
					unit:this.unit,
					rfCard:this.rfCardNo,
					rfTime:rfTime
				}
				this.Axios.post("/api/Event/add_assets_list",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					if(dat==1){
						
						this.$Message.success("操作成功");
						this.loadAsset();
						this.upload_file_data.splice(this.ind,1);
					}else{
						 this.$Message.error('操作失败');
					}
				})
			},unUnseDeal(){
				let data={
					id:this.id
				}
				
				this.Axios.post("/api/Event/upd_assets_canuse",data,{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					if(dat==1){
						this.$Message.success("操作成功");
						this.loadAsset();
						this.upload_file_data.splice(this.ind,1);
					}else{
						 this.$Message.error('操作失败');
					}
				})
			},loadFileDeal(param,ind){
				this.unsedTxt=param.id;
				this.ind=ind;
				this.assetId=param.assetId;
				this.assetName=param.assetName;
				this.assetType=param.assetType;
				this.branch=param.branch;
				this.canUse=param.canUse;
				this.count=param.count;
				this.type=param.type;
				this.department=param.department;
				this.id=param.id;
				this.isUsing=param.isUsing;
				this.keepPlace=param.keepPlace;
				this.keeper=param.keeper;
				this.model=param.model;
				this.orginalVal=param.orginalVal;
				this.purchase=param.purchase;
				this.remark=param.remark;
				this.startUse=param.startUse;
				this.unit=param.unit;

				if(param.deal=="发卡"){
					this.postCard=true;
				}else{
					this.unused=true;
				}
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
	#bank_property{
		width: 100%;
		height: 100%;
		padding: 0 15px;
		.num{
			color:#57a3f3 ;
		}
		.property_plan_deal{
			display: flex;
			justify-content: space-between;
			margin-bottom:15px ;
			.search{
				button{
					margin-left: 10px;
					padding: 0 15px;
					min-width: 71px;
					width: auto;
					color: #fff;
				}
				&.onlyOne{
					button{
						margin-left: 0px;
					}
				}
			}
			.btns{
				button{
					margin-left: 10px;
					padding: 0 15px;
					min-width: 71px;
					width: auto;
					color: #fff;
				}
				
			}
		}
	}
</style>


