<style lang="scss" scoped="scoped">
	#bank_card{
		width: 100%;
		height: 100%;
		padding: 0 15px;
		.inforWrap{
			height: 100%;
			.wrap{
				height: 100%;
				/*border:1px solid red;*/
			}
		}
		.ivu-tabs{
			height: 100%;
		}
		.right_modal{
			height: 100%;
			/*border:1px solid red;*/
			.rightTit{
				height:33%;
				h4{
					font-size: 15px;
					text-align: center;
					height: 40px;
					line-height: 40px;
				}
				
			}
			.count_mem_Infor_wrap{
				border: 1px solid  #dddee1;
				overflow: hidden;
				.count_mem_Infor{
					width: 100%;
					height: 50%;
					dl{
						width: 50%;
						padding: 10px 0;
						text-align: center;
						float: left;
						dt{
							padding: 10px 0;
							font-size: 15px;
						}
						dd{
							color: #2d8cf0;
							font-size: 16px;
						}
					}
				}
			}
			
		}
		
		.card_plan_deal{
			display: flex;
			justify-content: space-between;
			margin-bottom:15px ;
			.search{
				button{
					margin-left: 10px;
					padding: 0 15px;
					min-width: 71px;
					width: auto;
					color: #fff;
				}
				&.onlyOne{
					button{
						margin-left: 0px;
					}
				}
			}
			.btns{
				button{
					margin-left: 10px;
					padding: 0 15px;
					min-width: 71px;
					width: auto;
					color: #fff;
				}
				
			}
		}
		
	}
</style>
