					{title: '权限',key: 'author'}
				],
				printer_mark_col:[
					{title: '人员',key: 'name'},{title: '刷卡时间',key: 'time'},{title: '刷卡卡号',key: 'card'},{title: '打印色彩',key: 'color'},
					{title: '纸张类型',key: 'type'},{title: '纸张数量',key: 'num'},{title: '是否双面',key: 'double'},{title: '事件',key: 'event'}
				],
				loginNameList:[],
				gygList:[],
