				gyg_auth_data:[],//更衣柜权限管理
				gyg_auth_dsq_data:[],
				gyg_auth_dcq_data:[],
				gyg_select:"",//选择的更衣柜
				memCord:"",//进行操作的卡号
				gyg_title:"更衣柜分配",
