				oldGyg:"",
				tableHeight3:"",
				tableHeight4:"",
				login_name:"",
				system:"",
				admin_card:"",
				tableHeight:'',
				tableHeight2:"",
				memList:[],
				isadd:false,
				gyg_his_dat:[],
				dyj_auth_data:[],//打印机已授权
				dyj_auth_dsq:[],//打印机待授权
				dyj_auth_dcq:[],//打印机待撤权
