				isDele:false,
				Mjgroup:false,
				mj_group_deal:null,
				mjYsq:0,
				mjDsq:0,
				mjYcq:0,
				mjDcq:0,
				mj_title:"修改门禁权限",//门禁modal
		    	mj_modal:false,
		    	mj_sq:false,//true撤权
		    	mj_select:[],
		    	mjList:[],
		    	mj_first:"",
		    	mj_second:[],
		    	mj_group_name:"",
