				gygYsq:0,
				gygDsq:0,
				gygYcq:0,
				gygDcq:0,
				dyjYsq:0,
				dyjDsq:0,
				dyjYcq:0,
				dyjDcq:0,
				sysList:[
					{value:'0',label:'一卡通系统'},{value:'1',label:'门禁系统'},{value:'2',label:'梯控系统'},{value:'3',label:'更衣柜系统'},
					{value:'4',label:'消费系统'},{value:'5',label:'打印机系统'},{value:'6',label:'会议系统'},{value:'7',label:'访客系统'}
				],
				dyj_title:"打印机授权",//打印机modal标题
		    	dyj_modal:false,//打印机modal
		    	dyj_sq:false,//是否打印机授权
		    	dyjdeal:"",
		    	mem_search_txt:"",
		    	card_people_data_sort:[],
		    	admin_search_txt:"",
		    	card_admin_data_sort:[],
		    	admin_search_txt_mj:"",
		    	mj_author_data_sort:[],
		    	search_txt_gyg:"",
		    	gyg_auth_data_sort:[],
		    	search_txt_dyj:"",
		    	dyj_auth_data_sort:[]
				
			}
		},
		mounted(){
			let h = document.documentElement.clientHeight || document.body.clientHeight;
			this.tableHeight=h-180;
			this.tableHeight2=h-230;
			this.tableHeight3=(h-290)/3;
			this.tableHeight4=this.tableHeight2-this.tableHeight3-45;
			this.loadAdmin();
//			this.loadMemInf();
			this.loadLoginName();//获取软件登录名
//			this.loadMjGroupPa();
//			this.loadMjGroupSec();
//			this.loadAdminInf();
//			this.loadgygAuth();
			this.loadMjNum();
			this.loadMjGroupList();//添加门禁所有的门禁点mj_group_select,mjgroupList
			this.loadGygNum();//更衣柜权限概况
			this.loadDyjNum();//打印机权限概况
//			this.loadDyjAuth();//加载打印机
//			this.loadMjAuth();
//			this.loadMj_Sec();
//			this.loadMjApp();
		},watch:{
			
			mem_search_txt(){
				let _search=this.mem_search_txt;
				if(!_search){
					this.card_people_data_sort= this.card_people_data;
				}
			},
			admin_search_txt(){
				let _search=this.admin_search_txt;
				if(!_search){
					this.card_admin_data_sort= this.card_admin_data;
				}
			},
			admin_search_txt_mj(){
				let _search=this.admin_search_txt_mj;
				if(!_search){
					this.mj_author_data_sort= this.mj_author_data;
				}
			},
			search_txt_gyg(){
				let _search=this.search_txt_gyg;
				if(!_search){
					this.gyg_auth_data_sort= this.gyg_auth_data;
				}
			},
			search_txt_dyj(){
				let _search=this.search_txt_dyj;
				if(!_search){
					this.dyj_auth_data_sort= this.dyj_auth_data;
				}
			}
		},
		methods:{
			
			loadAdmin(){
				this.Axios.post("/api/Event/get_mem_infor_work",{workNo:localStorage.userName},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					for(var value of dat){
						if(value.type==0){
							this.iscard=true;
							this.loadMemInf();//人员管理加载数据
							this.loadAdminInf();//管理员加载数据
						}else if(value.type==1){
							this.ismj=true;
							this.loadMjAuth();//门禁加载权限管理
							this.loadMjGroupPa();//门禁分组一级
							this.loadMjGroupSec();//门禁分组二级
							this.loadMj_Sec();
							this.loadMjApp();
						}else if(value.type==3){
							this.isgyg=true;
							this.loadgygAuth();//更衣柜加载权限管理
						}else if(value.type==5){
							this.isdyj=true;
							this.loadDyjAuth()//打印机加载权限
						}
					}
				})
			},
			search_mem(){
				let _search=this.mem_search_txt;
				if(_search){
					let sort= this.card_people_data.filter(function(item){
						return Object.keys(item).some(function(key){
							return String(item[key]).toLowerCase().indexOf(_search)>-1
						})
					})
					this.card_people_data_sort=sort;
				}
			},		
			search_admin(){
				let _search=this.admin_search_txt;
				if(_search){
					let sort= this.card_admin_data.filter(function(item){
						return Object.keys(item).some(function(key){
							return String(item[key]).toLowerCase().indexOf(_search)>-1
						})
					})
					this.card_admin_data_sort=sort;
				}
			},
			search_admin_mj(){
				let _search=this.admin_search_txt_mj;
				if(_search){
					let sort= this.mj_author_data.filter(function(item){
						return Object.keys(item).some(function(key){
							return String(item[key]).toLowerCase().indexOf(_search)>-1
						})
					})
					this.mj_author_data_sort=sort;
				}
				
			},search_gyg_txt(){
//				gyg_auth_data_sort,search_txt_gyg
				let _search=this.search_txt_gyg;
				if(_search){
					let sort= this.gyg_auth_data.filter(function(item){
						return Object.keys(item).some(function(key){
							return String(item[key]).toLowerCase().indexOf(_search)>-1
						})
					})
					this.gyg_auth_data_sort=sort;
				}
				
			},search_dyj_txt(){
				let _search=this.search_txt_dyj;
				if(_search){
					let sort= this.dyj_auth_data.filter(function(item){
						return Object.keys(item).some(function(key){
							return String(item[key]).toLowerCase().indexOf(_search)>-1
						})
					})
					this.dyj_auth_data_sort=sort;
				}
			},
			admin_create(deal){
				this.admin_appointment=true;
			},
			refresh(){			
				this.loadMemInf();
			},
			loadMemInf(){
				this.Axios.get("/api/Event/get_mem_infor",{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.card_people_data=[];
					for(var value of dat){
						let arr={
							card:value.cardNo,
							code:value.workNo,
							name:value.name,
							sex:value.sex==1?'女':'男',
							depart:value.department,
							tel:value.phone,
							mail:value.mail
						}
						let mem={
							value:value.workNo,
							label:value.name
						}
						this.memList.push(mem)
						this.card_people_data.push(arr)
					}
					this.card_people_data_sort=this.card_people_data;

				})
			},
			loadLoginName(){
				this.Axios.get("/api/Event/get_all_userName",{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData;
					for(var value of dat){
						let obj={
							value:value,
							label:value
						}
						this.loginNameList.push(obj)
					}
					
				})
			},
			loadAdminInf(){
//				card_admin_data
				this.Axios.get('/api/Event/get_admin_infor',{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.card_admin_data=[];
					for(var value of dat){
						let arr={
							id:value.id,
							name:value.name,
							depart:value.department,
							code:value.workNo,
							sys:this.returnSys(value.type),
							userName:value.username
						}
						this.card_admin_data.push(arr)
					}
					this.card_admin_data_sort=this.card_admin_data;
				})
			},loadMjGroupList(){
				//所有的门禁点,mjgroupList
				this.Axios.get("/api/Event/get_mj_eq",{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					for (var value of dat){
						let obj={
							value:value.id,
							label:value.name
						}
						this.mjgroupList.push(obj)
					}
				})
			},
			loadMjAuth(){
				this.Axios.get("/api/Event/get_mj_auth",{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
					let dat=res.data.HttpData.data;
					this.mj_author_data=[];
					this.mj_auth_dsq=[];
					this.mj_auth_dcq=[];
					for(var value of dat){
						if(value.mjStatus==1){
							let obj={
								name:value.name,
								code:value.workNo,
								card:value.cardNo,
								depart:value.department,
								author:value.mjtxt,
								mjId:value.mj,
								mjStatus:value.mjStatus
							}
							this.mj_author_data.push(obj)
						}
						
						if(value.mjStatus==0){
							let obj={
								name:value.name,
								workNo:value.workNo,
								card:value.cardNo,
								mjStatus:value.mjStatus
							}
							this.mj_auth_dsq.push(obj)
						}
						if(value.mjStatus==2){
							let obj={
								name:value.name,
