				}

			},
			returnSys(id){
				let txt;
				switch (id)
				{
					case 0:
						txt="一卡通系统"
					break;
					case 1:
						txt="门禁系统"
					break;
					case 2:
						txt="梯控系统"
					break;
					case 3:
						txt="更衣柜系统"
					break;
					case 4:
						txt="消费系统"
					break;
					case 5:
						txt="打印机系统"
					break;
					case 6:
						txt="会议系统"
					break;
					case 7:
						txt="访客系统"
					break;
				}
				return txt;
			}
			
		}
	}
</script>




